import React from 'react';
import classes from './Post.module.css'


const Post = (props) => {
    return (<div>

            <div className={classes.item}>
                <div><img
                    src="data:image/jpeg;base64,"
                    alt=""/>
                    {props.message}
                </div>
                <div>
                    <span>like {props.likesCount}</span>
                </div>
            </div>

        </div>
    );
};

export default Post;